html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  background: linear-gradient(45deg, #ff1a66 0%, rgba(255, 26, 102, 0) 70%), linear-gradient(135deg, #0643f9 10%, rgba(6, 67, 249, 0) 80%), linear-gradient(225deg, #0df259 10%, rgba(13, 242, 89, 0) 80%), linear-gradient(315deg, #f99e1f 100%, rgba(249, 158, 31, 0) 70%);
  margin: 0;
  font-family: sans-serif;
}

.wrapper {
  max-width: 500px;
  margin: 0 auto;
  font-size: 20px;
  line-height: 2;
  position: relative;
}

a {
  color: #000;
  background: rgba(0, 0, 0, .05);
  border-radius: 20px;
  text-decoration: none;
}

.highlight {
  z-index: -1;
  background: #fff;
  border-bottom: 2px solid #fff;
  border-radius: 20px;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.menu {
  justify-content: center;
  margin: 100px 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu a {
  color: #000;
  margin: 0 20px;
  padding: 5px;
  display: inline-block;
}

